@use 'foundation' as *;

.p-technology-classroom {
  .l-section-inner {
    @include mq(ovsp) {
      max-width: calc(79.4rem + 3rem * 2);
    }
  }

  &-row {
    & + & {
      margin: 10rem 0 0;
    }

    h2 {
      margin: 0 0 3rem;
      padding: 0 0 1rem;
      font-size: 3.6rem;
      line-height: 1.5;
      font-weight: 500;
      border-bottom: 1px solid var(--color-gray);
    }

    h3 {
      font-size: 2.4rem;
      line-height: 1.5;
      font-weight: 500;
      margin: 0 0 1rem;
    }

    > * + * {
      margin: 3rem 0 0;
    }

    p {
      font-weight: 400;
    }

    > figure {
      width: fit-content;
      margin-inline: auto;
      border: 1px solid var(--color-gray);
    }

    ul {
      display: grid;
      gap: 1rem;
      margin-left: 1.6rem;

      li {
        list-style: disc;
        font-weight: 400;
      }
    }

    ol {
      display: grid;
      gap: 1rem;
      margin-left: 1.6rem;

      li {
        list-style: decimal;
        font-weight: 400;
      }

      ul {
        li {
          list-style: disc;
        }
      }
    }

    .gray-box {
      padding: 2rem;
      margin: 1rem 0 0;
      background-color: var(--color-gray-light);

      h4 {
        margin: 0 0 2rem; 
        font-size: 2rem;
        font-weight: 500;
      }
    }

    .col {
      display: flex;
      gap: 0 2rem;

      figure {
        flex-shrink: 0;
      }
    }

    .image-col {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      gap: 2rem;
    }
  }
}